import { Component, OnDestroy, OnInit } from '@angular/core';
import { OrganizationBasePage } from '../organization-base.page';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Select, Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { StorageService } from '../../@core/storage.service';
import { IElement } from '../../@store/global-info/global-info.model';
import { Observable, Subscription } from 'rxjs';
import { GlobalInfoState } from '../../@store/global-info/global-info.state';
import { OrganizationTypes } from '../organization-types';
import { TranslateService } from '@ngx-translate/core';
import { GetSportsInfo } from '../../@store/global-info/global-info.actions';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../../@core/inform.service';

// const SPORTS_MAX_ITEMS = 10;
const PAGE_POSITION = 2;
@Component({
  selector: 'app-sports-organization',
  templateUrl: './sports-organization.page.html',
  styleUrls: ['./sports-organization.page.scss'],
})
export class SportsOrganizationPage extends OrganizationBasePage implements OnInit, OnDestroy {
  public organizationType = OrganizationTypes.Sport;
  public sportsList: IElement[];
  public selectedItems: string[] = [];
  public title: string;
  private subscription$: Subscription;
  public allToggle = false;
  @Select(GlobalInfoState.getSports) sports$: Observable<IElement[]>;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    private translateService: TranslateService
  ) {
    super(location, router, store, utilsService, camera, informService, storage);
    if (this.router.getCurrentNavigation().extras.state) {
      this.organizationType = this.router.getCurrentNavigation().extras.state.organizationType;
    }
  }

  ngOnInit() {
    this.loadSportsInfo();

    this.title = this.translateService.instant(`sports-organization.title${this.getOption()}`);
  }

  private getOption() {
    switch (this.organizationType) {
      case OrganizationTypes.Sport:
        return OrganizationTypes.Sport;
      case OrganizationTypes.Academic:
        return OrganizationTypes.Academic;
      case OrganizationTypes.Other:
        return OrganizationTypes.Other;
      default:
        console.error('No type selected');
        break;
    }
  }

  private loadSportsInfo(): void {
    this.store.dispatch(new GetSportsInfo());
    this.subscription$ = this.sports$.subscribe((data) => {
      this.sportsList = data;
    });
  }

  public selectItem(id): void {
    if (this.isItemSelected(id)) {
      this.selectedItems = this.selectedItems.filter((item) => item !== id);
    } else {
      // if (this.selectedItems.length < SPORTS_MAX_ITEMS) {
      this.selectedItems.push(this.sportsList.find((item) => item.id === id)?.id);
      // }
    }
  }

  public isItemSelected(id): boolean {
    return this.selectedItems?.indexOf(id) !== -1;
  }

  // public reachMaxItemsSelected(): boolean {
  //   return this.selectedItems.length === SPORTS_MAX_ITEMS;
  // }

  public async goNextPage(): Promise<void> {
    if (this.selectedItems.length !== 0 || this.allToggle) {
      // await this.updateStorageDataObject('sport', this.allToggle ? ['-1'] : this.selectedItems);
    }
    this.goNextFlowPage(PAGE_POSITION);
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }

  public resetSelection() {
    if (this.allToggle) {
      this.selectedItems = [];
    }
  }

  private disconnectObservers(): void {
    this.subscription$.unsubscribe();
  }
}
